cardsData.sort(function () {
  return Math.random() - 0.5;
});

function Log(props) {
  return (
    <div className="Log">
      <span>Enter your name</span>
      <input onChange={props.getName} type="text" placeholder="your name" />
      {props.playerName.length > 0 ? (
        <button onClick={props.startGame}>Submit</button>
      ) : null}
    </div>
  );
}

class StartGame extends Component {
  constructor() {
    super();
    this.state = {
      deck: cardsData,
      playerHand: [],
      opponentHand: [],
      result: null,
      isOpponentTurn: false,
    };
  }

  takeCard = () => {
    const deck = [...this.state.deck];
    const card = deck.pop();
    if (!this.state.isOpponentTurn) {
      const playerHand = [...this.state.playerHand];
      playerHand.push(card);
      this.setState({
        deck: deck,
        playerHand: playerHand,
      });
    } else {
      const opponentHand = [...this.state.opponentHand];
      opponentHand.push(card);
      this.setState({
        deck: deck,
        opponentHand: opponentHand,
      });
    }
  };

  componentDidMount() {
    setTimeout(this.takeCard, 1000);
    setTimeout(this.takeCard, 1500);
  }

  handleHitBtn = () => {
    this.takeCard();
    console.log(this.state);
  };

  handleStandBtn = () => {
    this.setState({
      isOpponentTurn: true,
    });

    setTimeout(this.takeCard, 1000);
    setTimeout(this.takeCard, 1500);
    setTimeout(this.takeCard, 1800);
    setTimeout(this.takeCard, 2000);
  };

  render() {
    const { isOpponentTurn, deck, playerHand } = this.state;
    if (deck.length === 0) {
      alert("Deck is empty");
    }
    return (
      <div className="game-mode">
        <div className="game-field">
          <div className="card-place"></div>
          <div className="card-place"></div>
          <div className="card-place"></div>

          <img src={cardBackSide} alt="card-backside" />
          <img src={cardBackSide} alt="card-backside" />
        </div>
        <div className="player-hand">
          {this.state.playerHand.map((card) => {
            // let source = ;
            // <Images src={card.src} />;
            return <img src={require(`${card.src}`)} alt="card" />;
          })}
        </div>
        {/* <PlayerHand playerHand={playerHand} /> */}

        <button
          disabled={isOpponentTurn || deck.length === 0 ? true : false}
          onClick={this.handleHitBtn}
        >
          Hit
        </button>
        <button onClick={this.handleStandBtn}>Stand</button>
      </div>
    );
  }
}

export { Log, StartGame };
